@font-face {
  font-family: "Exil";
  src: local("Exil"),
    url(/static/media/exil.cbdfb905.ttf) format("truetype");
  font-weight: normal;
}

body {
  margin: 0;
  font-family: Exil;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.jumbotron{height:100%;background-color:var(--primaryColor);display:flex;flex-direction:column;align-items:start;justify-content:start}.jumbotron .text-container{padding-left:5%}.jumbotron .text-background{background-color:white;position:relative}.jumbotron .back{width:100%;height:100%;background-color:red}.jumbotron canvas{position:absolute;width:100%}.jumbotron .large-text{font-size:20vmin;line-height:auto;padding-top:0;margin-top:10vmin;padding-bottom:1%;background-color:black;color:white;mix-blend-mode:multiply}.jumbotron-center{height:100%;background-color:var(--primaryColor);display:flex;flex-direction:column;align-items:center;justify-content:center}.selection-container{width:70%;height:30%;display:flex;flex-wrap:wrap;justify-content:space-between}

.navbar{padding:1vmin;background-color:var(--primaryColor);display:flex;justify-content:flex-end}.navbar .nav-item{padding:2vmin}.navbar .logo-text{font-size:3vmin;font-weight:500;color:var(--textColor)}

.selection-box{min-width:300px;flex:0 1 40%;background-color:white;box-shadow:0 0 10px #5e5e5e;border-radius:40px;cursor:pointer;transition:1s;display:flex;flex-direction:column;align-items:center;justify-content:center}.selection-box h1{color:var(--primaryColor);font-size:5vmin}.selection-box:hover{transform:translateY(-10%);box-shadow:0 0 40px #5e5e5e}

:root{--bg: #fafdf6;--primaryColor: #000000;--secondaryColor: #b279a7;--tertiaryColor: #D387AB;--color4: #e899dc;--textColor: white}html,body,#root{height:100%;margin:0;color:white;color:var(--textColor)}.App{height:100%}.panel-bg{display:flex;flex-flow:column;width:100%;height:100%}.test{background-color:pink;flex:1 1 auto}.unselectable{-webkit-user-select:none;-ms-user-select:none;user-select:none}a{color:inherit;text-decoration:none}

